<template>
    <div class="works">
        <p class="content-title">{{info.title}}</p>
        <div class="content">
            <transition appear
            appear-active-class="custom-appear-active-class">
                <div class="work-warp">
                    <img :src="work.imgUrl">
                    <h3 class="work-info">{{work.explain}}</h3>
                    <div class="work-mask">
                        <a :href="work.url" target="_black">
                            <em>预览项目</em>
                            <span></span>
                            <span></span>
                            <span></span>
                            <span></span>
                        </a>
                    </div>
                </div>
            </transition>
            <div class="page">
                <router-link v-for='item in info.data' :key='item.id' :to="'/works/'+item.id">{{item.id}}</router-link>
            </div>
        </div>
    </div>
</template>

<script>
const url = 'https://www.easy-mock.com/mock/5c9b940ca88cf9475e399780/resume/works'
export default {
    data() {
        return {
            info: {},
            work: {}
        }
    },
    created() {
        this.$http(url).then(({ data }) => {
            this.info = data
            this.getWork()
        })
    },
    methods: {
        getWork() {
            const index = this.info.data.findIndex(item => item.id === Number(this.$route.params.id))
            this.work = this.info.data[index]
        }
    },
    watch: {
        $route() {
            this.getWork()
        }
    }
}
</script>

<style lang='less' scoped>
@import '../common/style/common.less';
    .content-title{
        animation-duration: 1s;
    }
    @keyframes bounce {
        0% {
            transform:rotate3d(0, 1, 0, 120deg);
        }
        40% {
            transform:rotate3d(0, 1, 0, -20deg);
        }
        60% {
            transform: rotate3d(0, 1, 0, 10deg);
        }
        80% {
            transform: rotate3d(0, 1, 0, -5deg);
        }
        100%{
             transform: rotateY(0deg);
        }
    }
    .custom-appear-active-class{
        animation: bounce 1s;
    }
    .works{
        .position;
        &::before{
            content: '';
            .position;
            background: rgba(0, 0, 0, 0.4);
            z-index: -1;
        }
        &:after{
            content: '';
            .position;
            background:url(../assets/bg/bs-docs-masthead-pattern.png) #3db8b8;
            z-index: -2;
            opacity: .7;
        }
    }
    .content{
        perspective: 600px;
        margin-top: 60px*@scale;
        display: flex;
        flex-direction: column;
        .work-warp{
            margin: auto;
            position: relative;
            box-sizing: border-box;
            overflow: hidden;
            width: 250px*@scale;
            height: 250px *@scale;
            box-shadow: 0 0 25px*@scale #000;
            img{
                transition: .5s;
                width: 100%;
            }
            &:hover{
                img{
                    transform: scale(1.3);
                }
                .work-mask{
                    opacity: 1;
                }
            }
        }
    }
    .work-mask{
        transition: .5s;
        opacity: 0;
        .position;
        background: rgba(0, 0, 0, .5);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        a{
            box-sizing: border-box;
            width: 100px;
            height: 40px;
            color: #fff;
            line-height: 40px;
            text-align: center;
            margin-top: 20px;
            background: rgba(0, 0, 0, .4);
            position: relative;
            overflow: hidden;
            em{
                font-size: 14px;
            }
            span{
                position: absolute;
                box-sizing: border-box;
                transition: .5s;
                background: #fff;
                &:nth-of-type(1){
                    width: 100%;
                    height: 2px;
                    left: 100%;
                    top: 0px;
                }
                &:nth-of-type(2){
                    width: 2px;
                    height: 100%;
                    right: 0px;
                    top: -100%;
                }
                &:nth-of-type(3){
                    width: 100%;
                    height: 2px;
                    bottom: 0px;
                    left: -100%;
                }
                &:nth-of-type(4){
                    width: 2px;
                    height: 100%;
                    left: 0px;
                    top: 100%;
                }
            }
            &:hover{
                span{
                    &:nth-of-type(1){
                        left:0px;
                    }
                    &:nth-of-type(2){
                        top: 0px;
                    }
                    &:nth-of-type(3){
                        left:0px;
                    }
                    &:nth-of-type(4){
                        top:0px;
                    }
                }
            }
        }
    }
    .work-info{
        font-family: '微软雅黑';
        color: rgb(209, 207, 207);
        font-size: 14px*@scale;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        text-align: center;
        z-index: 222;
        margin-top: 80px;
    }
    .page{
        text-align: center;
        font-family: '微软雅黑';
        margin-top: 30px;
        a{
            width: 25px*@scale;
            height: 25px*@scale;
            text-align: center;
            display: inline-block;
            line-height: 25px*@scale;
            background: rgb(82, 81, 81);
            margin: 0 5px*@scale;
            color: #fff;
            transition: .2s background, .2s color;
            &.a-active{
                background: #fff;
                color: #000;
            }
        }
    }
    @media all and (min-width:375px){
        .content{
            margin-top: 100px;
        }
    }
    @media all and (min-width:768px){
        @scale:1.6;
        .content{
            .work-warp{
                width: 300px;
                height: 300px;
                box-shadow: 0 0 25px*@scale #000;
            }
        }
        .work-info{
            font-size: 14px*@scale;
        }
        .work-mask{
            a{
                width: 100px*@scale;
                height: 40px*@scale;
                line-height: 40px*@scale;
                margin-top: 20px*@scale;
                em{
                    font-size: 14px*@scale;
                }
            }
        }
        .page{
            margin-top: 30px*@scale;
            a{
                width: 25px*@scale;
                height: 25px*@scale;
                line-height: 25px*@scale;
                margin: 0 5px*@scale;
            }
        }
    }
    @media all and (min-width:996px){
        .content{
            margin-top: 80px;
        }
    }
</style>
